*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

input,
select,
textarea,
button {
  font-family: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip-path: rect(0 0 0 0);
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.page {
  height: 100%;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 0%;
  min-height: 100%;
  font-family: "gotham_proregular", "Arial", sans-serif;
  background-color: #1c1c1e;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.header__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
  padding-right: 5.5%;
  padding-left: 8.5%;
  padding-top: 40px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
}

.logo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.logo__link {
  margin-right: 58px;
}

.lang {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 24px;
}

.lang__link {
  font-size: 20px;
  line-height: 19px;
  color: #d2aa74;
  text-transform: uppercase;
}
.lang__link--active {
  color: #ffffff;
}

.main-nav__button .main-nav__icon {
  stroke: #333333;
}
.main-nav__button .main-nav__icon-cross {
  display: none;
}
.main-nav__button .main-nav__icon-menu {
  display: block;
}
.main-nav__button--open .main-nav__icon-cross {
  display: block;
}
.main-nav__button--open .main-nav__icon-menu {
  display: none;
}
@media (min-width: 1024px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__link {
  font-size: 12px;
  line-height: 11px;
  color: #ffffff;
  transition: all 0.3s ease;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  border-right: 1px solid #ffffff;
}
.main-nav__list--open {
  display: flex;
  flex-direction: column;
  row-gap: 50px;
}
.main-nav__list .swiper-pagination-bullet {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: auto;
  height: auto;
  border-radius: 0;
  background-color: transparent;
  opacity: 1;
  text-align: right;
}
.main-nav__list .swiper-pagination-bullet .main-nav__link {
  color: #707070;
}
.main-nav__list .swiper-pagination-bullet-active .main-nav__link {
  font-size: 20px;
  line-height: 19px;
  color: #ffffff;
}
@media (min-width: 1024px) {
  .main-nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    row-gap: 50px;
  }
}

.main-nav {
  position: fixed;
  top: 0;
  right: 8%;
  z-index: 10;
  display: flex;
  align-items: center;
  height: 100%;
}

.main-nav__item {
  position: relative;
  padding-right: 23px;
  color: #ffffff;
}
.main-nav__item::after {
  content: "";
  position: absolute;
  right: -4px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 7px;
  height: 7px;
  background-color: #fff;
  border-radius: 50%;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  right: -11px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  border: 1px solid #fff;
}

.promo {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background-image: url("../images/promo-bg.svg"), url("../images/promo-bg-2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  padding-top: 85px;
  padding-bottom: 220px;
}

.promo__title {
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-size: 70px;
  line-height: 85px;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__title {
    max-width: 630px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-size: 150px;
    line-height: 0.8;
  }
}

.promo__text {
  margin: 0;
  margin-bottom: 17px;
  font-size: 14px;
  line-height: 13px;
}
@media (min-width: 768px) {
  .promo__text {
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 19px;
  }
}

.promo__main-text {
  max-width: 425px;
  margin: 0 auto;
  font-size: 9px;
  line-height: 13px;
  /* or 144% */
  text-align: center;
}
@media (min-width: 768px) {
  .promo__main-text {
    max-width: 500px;
    font-size: 12px;
    line-height: 16px;
  }
}
.promo__main-text p {
  margin: 0;
}

.timeline-section {
  padding-top: 120px;
  background-image: url("../images/timeline-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.timeline-section__wrapper {
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
}

.timeline {
  display: grid;
  grid-template-rows: 1fr min-content;
}

.timeline__list-years {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.timeline__list-years::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 3px;
  background: #fad398;
}

.timeline__list-year {
  position: relative;
  padding-bottom: 40px;
  max-width: 101px;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  /* identical to box height, or 133% */
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.timeline__list-year::after {
  content: "";
  position: absolute;
  bottom: -13px;
  left: 50%;
  margin-left: -12px;
  display: block;
  width: 24px;
  height: 24px;
  border: 3px solid #fad398;
  border-radius: 50%;
  background-color: #1c1c1f;
  z-index: 2;
}

.timeline__year-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  color: inherit;
}

.collection {
  padding-top: 120px;
  background-image: url("../images/item-bg.svg");
  background-repeat: repeat;
  background-size: cover;
}

.collection__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
  padding-right: 5.5%;
  padding-left: 8.5%;
}

.collections__catalog {
  display: grid;
  grid-template-columns: 1fr 1fr 1.2fr;
  gap: 2px;
  align-items: flex-start;
  max-width: 1000px;
}

.collections__column {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.collections__item {
  display: flex;
  justify-content: center;
  background-color: #1c1c1e;
  align-items: center;
  padding: 20px;
}
.collections__item--full {
  width: 100%;
}
.collections__item--half {
  width: calc(50% - 5px);
}
.collections__item--third {
  width: 32%;
}
.collections__item--six {
  width: 66%;
}
.collections__item a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
}
.collections__item img {
  max-width: 100%;
  height: auto;
}

.collection__head {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.collection__title {
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 48px;
  text-transform: uppercase;
  color: #ffffff;
  grid-column: 1/2;
}
@media (min-width: 1024px) {
  .collection__title {
    font-size: 80px;
    line-height: 97px;
  }
}

.collection__text {
  font-size: 10px;
  line-height: 12px;
  grid-column: 1/2;
}
@media (min-width: 768px) {
  .collection__text {
    font-size: 20px;
    line-height: 22px;
  }
}
.collection__text p {
  max-width: 490px;
}

.collection__table {
  grid-row: 1/3;
  grid-column: 2/3;
  padding-top: 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 23px;
}
.collection__table table tr td {
  font-weight: 400;
}
.collection__table table tr td:first-child {
  font-weight: 300;
}

.item {
  padding-top: 120px;
  background-image: url("../images/item-bg.svg");
  background-repeat: repeat;
  background-size: cover;
  background-attachment: fixed;
}

.item__article {
  min-height: 100vh;
}